@import url(https://fonts.googleapis.com/css2?family=Patrick+Hand+SC&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

p {
  margin: 0;
}

.App {
  text-align: center;
  height: 100vh;
  width: 100vw;
}

.App-Name {
  font-family: 'Patrick Hand SC', cursive;
}

.ButtonBar {
  width: auto;
  
  display: inline-flex;

  justify-content: space-evenly;

  margin: 6px;
}

.logo {
  height: 100px;
}

.right {
  float: right;
}

img {
  height: 100px;
}

@media only screen and (max-width: 600px) {
  .logo {
    height: 70px;
  }
}
.SecretSanta {
    display: flex;
    flex-direction: column;
    height: 100%;
}

.SecretSanta > .Header {
    flex: 0 1 auto;
}

.SecretSanta > .EditPage,
.SecretSanta > .ResultPage {
    flex: 1 1 auto;

    width: 100%;

    max-height: calc(100% - 116px - 70px);
}

.SecretSanta > .Footer {
    flex: 0 1 70px;
}

.EditPage,
.ResultPage {
    display: flex;
    flex-direction: column;
    height: 100%;
}

.EditPage > .ButtonBar,
.ResultPage > .ButtonBar {
    flex: 0 1 40px;
}

@media only screen and (max-width: 600px) {
    .SecretSanta > .EditPage,
    .SecretSanta > .ResultPage {
        max-height: calc(100% - 86px - 70px);
    }
}
.StartPage {
    flex: 1 1 auto;
    display: flex;

    justify-content: center;
}

.StartPage > div {
    display: flex;
    
    justify-content: center;
    align-items: center;
}
.SantaInput {
    display: flex;

    justify-content: center;

    margin: 6px;
}

.SantaInput > input {
    width: 200px;
}

.EditPage > .SantaInput {
    flex: 0 1 40px;
}
.EditCard button {
    margin-left: 3px;
}
.SantaCard {
    margin: 3px;

    min-height: 72px;
}

.SantaCard .card-title {
    line-height: 36px;
}

.SantaTable {
    width: auto;
    flex: 1 1 auto;
    margin: 6px;
    padding: 6px;
    overflow-y: scroll;

    border: solid black 1px;
}

.SantaTable > .row {
    margin: 0px;
}

.SantaTable > .row > .col {
    padding: 3px;
}

@media only screen and (max-width: 600px) {
}

.result-note {
    font-size: smaller;
}



.Footer-links > button {
    color: black;
}

.Footer-links > button:hover {
    color: #555;
}

.Changelog {
    text-align: left;
}

@media only screen and (max-width: 600px) {
    .Footer-links > button {
        font-size: smaller;
        padding-left: 6px;
        padding-right: 6px;
    }
}
